// import React from "react";
import { useState } from "react";
import { Container, Row, Col, Card, Button, ListGroup } from "react-bootstrap";
import profileImage from "../images/profile.jpg"; // <-- Replace with your image path
import microsoft_logo from "../images/microsofticon.png"; // <-- Replace with your image path
import { FaLinkedin, FaGithub, FaYoutube, FaFacebook, FaTiktok, FaUserTie } from "react-icons/fa"; // social icons
import { FaUniversity, FaBrain, FaChalkboardTeacher } from "react-icons/fa";
import { Link, useLocation } from 'react-router-dom';
import './DriveVideoEmbed.css';
function HomeScreen() {
  const [activated, setActivated] = useState(false);
  return (
    <Container className="py-0">
      {/* ===== Hero Section ===== */}
      <Row style={{background:"#0A0A1A"}} className="align-items-center mb-2">
       
        {/* Text Side */}
        <Col md={7} sm={12} className="text-center text-md-start mb-4 mb-md-0">
          {/* <p className="text-uppercase text-muted mb-1 fs-6">
            Welcome to my world
          </p> */}
          <h1 className="fw-bold display-5 mb-3">
            <span style={{color:"#00FFFF"}} > Hi, I'm {"{DUY VAN}"}</span>
            <br />
            <span style={{color:"#00FFFF"}} >M.Sc. in Data Science</span>
          </h1>

          <ListGroup variant="flush"  className="text-light fs-5 mt-3">
            <ListGroup.Item  className="fs-6 text-light bg-transparent border-0 d-flex align-items-center">
              <FaUniversity className="me-3 text-info fs-4" />
              M.Sc. in Data Science at LJMU (ENG)
            </ListGroup.Item>

            <ListGroup.Item className="fs-6 text-light bg-transparent border-0 d-flex align-items-center">
              <FaBrain className="me-3 text-warning fs-4" />
              10 Years of Data Analytics, AI
            </ListGroup.Item>

            <ListGroup.Item className="fs-6 text-light bg-transparent border-0 d-flex align-items-center">
              <FaUserTie  className="me-3 text-success fs-5" />
              7 Years in Data Manager / Head roles
            </ListGroup.Item>

            <ListGroup.Item className="fs-6 text-light bg-transparent border-0 d-flex align-items-center">
              <FaChalkboardTeacher className="me-3 text-primary fs-4" />
              3 Years Teaching SQL, Python, Power BI
            </ListGroup.Item>
          </ListGroup>

          {/* Social Media Links */}
          <div className="d-flex justify-content-center justify-content-md-start mt-4 mb-4 gap-3">
            <a
              href="https://www.linkedin.com/in/duyvanda/"
              target="_blank"
              rel="noopener noreferrer"
              className="text-decoration-none text-primary fs-3"
            >
              <FaLinkedin />
            </a>
            <a
              href="https://github.com/duyvanda"
              target="_blank"
              rel="noopener noreferrer"
              className="text-decoration-none text-primary fs-3"
            >
              <FaGithub />
            </a>
            <a
              href="https://www.youtube.com/@duyvandata"
              target="_blank"
              rel="noopener noreferrer"
              className="text-decoration-none text-danger fs-3"
            >
              <FaYoutube />
            </a>
            
            <a
              href="https://www.tiktok.com/@duyvanda"
              target="_blank"
              rel="noopener noreferrer"
              className="text-decoration-none text-danger fs-3"
            >
              <FaTiktok />
            </a>

            <a
              href="https://www.facebook.com/duyvandata/"
              target="_blank"
              rel="noopener noreferrer"
              className="text-decoration-none text-danger fs-3"
            >
              <FaFacebook />
            </a>            


          </div>

          <div className="mt-2 mb-2 d-flex gap-2 justify-content-center justify-content-md-start">
            <Link to="/sql_query">
            <Button variant="primary">
              Hc SQL C Bn (FREE)
            </Button>
            </Link>
            
          {/* <Link to="/test">
            <Button variant="success">
              Contact Me
            </Button>
          </Link> */}
          </div>
        </Col>

       {/* Image Side */}
        <Col md={5} sm={12} className="text-center mt-2">
          <div
            style={{
              overflow: "hidden",
              borderRadius: "50%",
              width: "100%",
              maxWidth: "400px",
              aspectRatio: "1 / 1",
              margin: "0 auto",
              boxShadow: "0 0 30px rgba(0,0,0,0.1)",
            }}
          >
            <img
              src={profileImage}
              alt="Profile"
              className="img-fluid"
              style={{
                width: "100%",
                height: "100%",
                objectFit: "cover",
              }}
            />
          </div>
        </Col>

      </Row>

    
    <div className="custom-video-wrapper" onClick={() => setActivated(true)} > 
      
      {/* 1. Tiu   trn */}
      

      <div className="video-header-bar"> {/* Thm div ny cho Header */}
        <div className="video-title">CHO MNG BN !!!</div>
      </div>

      

      {/* 2. Container cha iframe (m bo responsive) */}
      <div className="video-iframe-container">
        <iframe
          src="https://drive.google.com/file/d/10AYAJV0CxzxLQn7Kk-Tak7q5mW3zWSkq/preview?rm=minimal"
          width="100%"
          height="100%"
          title="title"
          frameBorder="0"
          allow="autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
        <div className="custom-logo-overlay">
          <img src={microsoft_logo} alt="Your Logo" className="overlay-logo" />
        </div>
      </div>
      {/* 3. Thng tin b sung  di */}
      <div className="video-meta-info">
        <span className="category">Category</span>
        <span className="code">Code</span>
      </div>
    </div>

    <div className="custom-video-wrapper"> 
      <div className="video-header-bar">
        <div className="video-title">POWER OF DATA !!!</div>
      </div>
      <div className="video-iframe-container">
        <iframe
          src="https://drive.google.com/file/d/1L9Q0awq_dLYRDh1pvJuiKJPLpL7V9JtL/preview?rm=minimal"
          width="100%"
          height="100%"
          title="title"
          frameBorder="0"
          allow="autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
        <div className="custom-logo-overlay">
          <img src={microsoft_logo} alt="Your Logo" className="overlay-logo" />
        </div>
      </div>
      <div className="video-meta-info">
        <span className="category">Category</span>
        <span className="code">Code</span>
      </div>
      </div>



      {/* ===== Services Section ===== */}
      <section>
        <h3 className="text-uppercase text-danger fw-bold mb-3 bg-dark ">Features</h3>
        <h2 className="fw-bold mb-4 bg-success">What I Teach</h2>

        <Row className="g-4">
          <Col md={4}>
            <Card className="shadow-sm h-100 text-center border-1">
              <Card.Body>
                <div className="fs-1 mb-3"></div>
                <Card.Title>SQL</Card.Title>
                <Card.Text>
                  Master data extraction, transformation, and analysis using SQL.
                  From basic SELECTs to complex window functions.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

          <Col md={4}>
            <Card className="shadow-sm h-100 text-center border-1">
              <Card.Body>
                <div className="fs-1 mb-3"></div>
                <Card.Title>Python</Card.Title>
                <Card.Text>
                  Learn Python for data analysis, automation, and integrating with SQL databases.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

          <Col md={4}>
            <Card className="shadow-sm h-100 text-center border-1">
              <Card.Body>
                <div className="fs-1 mb-3"></div>
                <Card.Title>Power BI</Card.Title>
                <Card.Text>
                  Build stunning dashboards and reports to visualize insights from your data.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </section>
    </Container>
  );
}

export default HomeScreen;
